import * as React from 'react'
import GameCard from './cards/gamecard'
import { graphql, useStaticQuery } from 'gatsby'



const GameCabaret  = () => {

    function getUrl(game) {

        if(game.node.original.src.includes("Sword"))
            return "https://ricksrick.itch.io/get-my-sword";
        if(game.node.original.src.includes("gun")) 
            return "https://ricksrick.itch.io/glitchgun";
        return "";
    }

    const data = useStaticQuery(graphql`
        query MyQuery {
            allImageSharp {
                edges {
                node {
                    original {
                    src
                    }
                    gatsbyImageData
                    id
                }
                }
            }
        }
    `)

    const games = data.allImageSharp.edges.filter((a) => {
        return a.node.original.src.includes("Cover")});


    return (
        <>
        {
            games.map((game) => (                
                <GameCard 
                    key={game.node.id}
                    image={game.node.gatsbyImageData}
                    url = {getUrl(game)}
                />
            ))
        }        
        </>
    );
}

export default GameCabaret