import * as React from 'react'
import { useState, useEffect } from 'react';
import ProjectCard from './cards/projectcard'
import JSONData from "../../content/github_project.json"

const bannedRepoID = [
    475058137,
    452007496,
]


const ProjectCabaret  = ({number}) => {

    const [repos , setRepos] = useState(null);
  
    useEffect(() => {
        const fetchData = async () => {
            const response = await fetch(`https://api.github.com/users/ricksrick/repos`);
            const newData = await response.json();
            
            if(newData["message"] === undefined)
                setRepos(newData);
            else 
                setRepos(null);
        };
        fetchData();
    }, []);

    const source = (repos !== null) ? repos : JSONData;

    let data = source.sort((a, b) => (new Date(a.updated_at).getFullYear()) < (new Date(b.updated_at).getFullYear()) ? 1 : -1)
               .filter(item => bannedRepoID.indexOf(item.id) <= -1)
               .slice(0, number);

    return (
        <>
        {
            
            data.map((repo) => (
                <ProjectCard  
                    key={repo.id}
                    year={(new Date(repo.updated_at).getFullYear())} 
                    projectTitle={repo.name} 
                    description={repo.description} 
                    most_used_language={repo.language} 
                    url={repo.html_url}/>
            ))
        }
        </>
    );

}

export default ProjectCabaret