import * as React from 'react'
import { GatsbyImage } from 'gatsby-plugin-image'

const GameCard = ({image, url}) => {

    return (
        <a href={url} aria-label={url}>
            <div className="col">
                <div className="card gamecard">      
                    <GatsbyImage 
                        image = {image} 
                        alt=""
                    />
                </div>
            </div>
        </a>
    )
}

export default GameCard