import * as React from 'react'
import Icon from "../../images/project-link.svg";
import { useState, useEffect } from 'react';

function language(l) {
    
    if(l === undefined) return undefined;

    switch(l){
        case 'c++': return 'cpp';
        case 'c#' : return 'cs';
        default: return l;
    }
}

const dummy = {
  "title": "Sample Title",
  "badge": `my-cs-badge`,
  "lang": "c#",
  "year": "2020",
  "description": "A short description about code",
  "url": "me/"
}

const ProjectCard = ({projectTitle, year, most_used_language, description, url}) => {

    const [data , setData] = useState(dummy);

    useEffect(() => {
      setData({
        "title": projectTitle,
        "badge": `my-${language(most_used_language?.toLowerCase())}-badge`,
        "lang": most_used_language,
        "year": year,
        "description": description,
        "url": url
      })
    }, [projectTitle, year, most_used_language, description, url]);

    return (
        <div className="col">
          <div className="card h-100">
            <div className="card-body">

              <div className='d-flex justify-content-between mb-4'>
                <span>{data.year}</span>
                <span className={`badge ${data.badge}`}>{data.lang}</span>
              </div>

              <p style={{fontSize: 1.25 + "rem", marginBottom: 0.5 + "rem", lineHeight: 1.2}} className="card-title"><b>{data.title}</b></p>
              <p className="card-text">{data.description}</p>
            </div>
            <div className="card-footer">
              <a aria-label='access site' href={data.url} className="btn btn-primary my-second-button prog-button align-content-center" type="button">
                <div style={{display: 'flex', alignItems: 'center', height: 100 + '%'}}>
                  <div style={{marginRight: 10 + "px"}}>Page</div><Icon/></div></a>
            </div>
          </div>
        </div>
    )

}

export default ProjectCard